            <!-- <a href="#" style="font-size: 12px;">Online First, doi:10.32604/cmes.2020.011067 </a> -->
            <h3>
               Span Identification and Technique Classification of Propaganda in News Articles
            </h3>
            <span style="display: block;color: #4f5671;line-height: 25px;margin-bottom: 10px;font-size: 12px;">
                Wei Li1,  Shiping Wen2
            </span>
            <ul style="padding: 0;font-size: 12px;color: #4f5671;">
                <li>1 School of Computer Science and Engineering, University of Electronic Science and Technology of China, Chengdu, 611731, China</li>
                <li>2 Faculty of Engineering and Information Technology, University of Technology Sydney, NSW, 2007, Australia</li>
            </ul>
            <span style="font-size: 12px;color: #4f5671;">∗ Corresponding Author: Shiping Wen. </span>
        </div>

        <div class="download">
            <span><a href="https://stduestceducn-my.sharepoint.com/:b:/g/personal/201921080433_std_uestc_edu_cn/EX2SYQuIGLBJtkLixJxXmvgBYf5v-Y2tBoOs4R4SFJsxbw?e=s8USlT" rel="nofollow" class="pdf">Download PDF</a>  </span>
        </div>

        <div class="abstract">
            <h3>Abstract</h3>
            <p>
               Propaganda is a rhetorical technique designed to serve a specific topic, which is often used purposefully in news articles
to achieve our intended purpose because of its specific psychological effect. Therefore, it is significant to be clear where
and what propaganda techniques are used in the news for people to understand its theme efficiently during our daily
lives. Recently some relevant researches are proposed for propaganda detection but unsatisfactory. As a result, detection
of propaganda techniques in news articles is badly in need of research. In this paper, we are going to introduce our
systems for detection of propaganda techniques in news articles, which is split into two tasks, Span Identification (SI)
and Technique Classification (TC). For these two tasks, we designed a system based on the popular pretrained BERT
model respectively. Experiments on the dataset of about 550 news articles offered by SEMEVAL show that our systems
perform state-of-the-art.
            </p>
        </div>

        <div class="keywords">
            <h3>Keywords</h3>
            <p>Propaganda detection, neural network, span identification, technique classification, bert.</p>
